html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  background-color: var(--color-white);
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
  font-size: 18px;
  color: #242424;
  &.lock{
    overflow: hidden;
  }
  min-width: 320px;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

.list-reset {
  list-style: none;
  padding: 0;
  margin: 0;
}

.site-container {
  overflow: hidden;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
  padding: 0;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1260px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}
